@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200;0,500;0,700;1,200;1,500;1,700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 1, 700;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: rgba(228, 233, 236, 0.8274509804);
  color: #091421;
}

*, *:before, *:after {
  box-sizing: inherit;
}